@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Lora:wght@500&display=swap');

.sent {
  flex-direction: row-reverse;
  justify-self: flex-end;
  position: relative;
  right: 0;
}

.sent > div > p {
  align-self: flex-end;
  color: #ffffff;

  display: flex;
  justify-content: flex-end;
}

.sent > div {
  background-color: #60a5fa;
  border-color: #60a5fa;
}
.received > div {
  background-color: #bfdbfe;
  border-color: #bfdbfe;
}
.received > div > p {
  color: #0e0e0e;

}

.profile-image {
  border-radius: 50%;
}
